/* eslint-disable no-undef */
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../components/helpers/field";
import { getTglSystem } from "../../../components/helpers/function";
import Tabel from "../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../Validasi/ValidasiTanggalLaporan";
// import Loading from "react-fullscreen-loading";
import LaporanPesananPdf from "./pdf/LaporanPesananPdf";
// import LaporanPesananExel from "./exel/LaporanPesananExel";
class HeadLaporanPesanan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      columns: [
        {
          dataField: "no_pesanan",
          text: "No Pesanan",
          footer: "",
          footerAttrs: {
            colSpan: "3",
          },
        },
        {
          dataField: "kode_sales",
          text: "Sales",
        },
        {
          dataField: "tanggal",
          text: "Tgl Pesan",
        },
        {
          dataField: "total_berat",
          text: "Total Berat",
          headerClasses: "text-right",
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData.reduce((acc, item) => acc + item, 0).toFixed(2)}{" "}
            </div>
          ),
          formatter: (data) => {
            return (
              <div className="text-right"> {data.toFixed(2)}</div>
            );
          },
        },
        {
          dataField: "total_qty",
          text: "Total Qty",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right"> {data}</div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData
                .reduce((acc, item) => acc + item, 0)}{" "}
            </div>
          ),
        },
        {
          dataField: "ongkos",
          text: "Ongkos",
          headerClasses: "text-right",

            formatter: (data) => {
              return (
                <div className="text-right"> {data.toLocaleString("kr-ko")}</div>
              );
            },
            footer: (columnData) => (
              <div className="text-right">
                {" "}
                {columnData
                  .reduce((acc, item) => acc + item, 0)
                  .toLocaleString("kr-KO")}{" "}
              </div>
            ),
        },
        {
          dataField: "total_harga",
          text: "Total Rp",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right"> {data.toLocaleString("kr-ko")}</div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData
                .reduce((acc, item) => acc + item, 0)
                .toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },

        {
          dataField: "jumlah_bayar",
          text: "Bayar Rp",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right"> {data.toLocaleString("kr-KO")}</div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData
                .reduce((acc, item) => acc + item, 0)
                .toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
        {
          dataField: "sisa_bayar",
          text: "Sisa Rp",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right"> {data.toLocaleString("kr-KO")}</div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData
                .reduce((acc, item) => acc + item, 0)
                .toLocaleString("kr-KO")}{" "}
            </div>
          ),
        },
      ],
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    LaporanPesananPdf(this.props.LaporanPesanan);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
              onFocus={() => setTimeout(() => {
                HideKeyboard.postMessage("")
              }, 100)}
            />
          </div>

          <div className="col-lg-4">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
              onFocus={() => setTimeout(() => {
                HideKeyboard.postMessage("")
              }, 100)}
            />
          </div>

          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="no_pesanan"
              data={this.props.export ? this.props.LaporanPesanan : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan Titip Uang Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-12">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              {/* <div className="col-lg-6">
                <LaporanPesananExel
                  data={this.props.LaporanPesanan}
                />
              </div> */}
            </div>
          </div>
        </div>
        {/* {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null} */}
      </form>
    );
  }
}

HeadLaporanPesanan = reduxForm({
  form: "HeadLaporanPesanan",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanPesanan);
// export default connect()(HeadLaporanPesanan);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanPesanan);
