import {
    React,
    connect,
    Link,
    reduxForm,
    Panel,
    PanelHeader,
    getData2,
      formatDate,
      PanelBody,
    moment,
    ToastNotification,
    localStorageencryp,
  } from "../../../components/helpers/library";
import { PageSettings } from "../../../config/page-settings";
  import HeadLaporanPesanan from "./HeadLaporanPesanan";
  
  class LaporanPesanan extends React.Component {
    static contextType = PageSettings;
    constructor(props) {
      super(props);
      this.state = {
        isLoading: false,
        LaporanPesanan: [],
        export: false,
      };
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    handleSubmit(data) {
      let data_post = {
        tgl_awal: formatDate(
          moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
        ),
        tgl_akhir: formatDate(
          moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
        ),
      };
      this.setState({
        isLoading: true,
      });
      getData2(
        "pesanan/reports?tgl_from=" +
          data_post.tgl_awal +
          "&tgl_to=" +
          data_post.tgl_akhir
      )
        .then((res) => {
          // console.log(res);
          if (res.data.length === 0) {
            ToastNotification("info", "Laporan Pesanan Kosong");
            this.setState({
              export: false,
              isLoading: false,
            });
         
            return false;
          }
          ToastNotification("success", "Laporan Pesanan Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            LaporanPesanan: res.data,
            isLoading: false,
            export: true,
          });
        })
        .catch((err) => {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            export: false,
            isLoading: false,
          });
        });
    }
  
    componentDidMount() {
      this.context.handleSetPageSidebar(false);
      this.context.handleSetPageHeader(false);
  }

    render() {
      return (
        <div>
           <Link to="/SubMenuLaporan">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
          <Panel>
            <PanelHeader>Laporan Pesanan</PanelHeader>
            <br />
            {/* Blank Page */}
            <PanelBody>
              <HeadLaporanPesanan
                export={this.state.export}
                isLoading={this.state.isLoading}
                LaporanPesanan={this.state.LaporanPesanan}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </PanelBody>
  
            <br />
            {/* End Tambah Blank Page  */}
          </Panel>
        </div>
      );
    }
  }
  
  LaporanPesanan = reduxForm({
    form: "LaporanPesanan",
    enableReinitialize: true,
  })(LaporanPesanan);
  export default connect(null)(LaporanPesanan);
  